import React, { useCallback, useEffect, useState } from "react";
import { Box, Paper, Slide, Typography, useTheme } from "@material-ui/core";
import RecreatePasswordSquare from "./RecreatePasswordSquare";
import { useDispatch } from "react-redux";
import {
  fireSnake,
  recreatePassword,
  validateResetPassword,
} from "../../store/actions";
import PageSpinner from "../../components/UI/Spinners/PageSpinner";
import { useTranslation } from "react-i18next";
import PageError from "../../components/common/PageError";

const ForgotPassword = ({ match, history }) => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const theme = useTheme();
  const email = match.params ? match.params.email : "";
  const token = match.params ? match.params.token : "";
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const validate = useCallback(async () => {
    try {
      await dispatch(validateResetPassword(email, token));
    } catch (err) {
      setError(err);
    } finally {
      setLoading(false);
    }
  }, [dispatch, email, token]);

  useEffect(() => {
    validate();
  }, [validate, email, token]);

  const onNewPassword = async (pass) => {
    try {
      await dispatch(recreatePassword(email, token, pass));
      history.replace("/");
      dispatch(fireSnake("success", t("common:pass_reset_success")));
    } catch (err) {
      dispatch(fireSnake("error", err.message));
    }
  };

  return (
    <div
      className={`${theme.css.gradient_primary} fullheightVH overflow_hidden`}
    >
      {loading ? (
        <Paper className="position__centerlize center p-3">
          <PageSpinner />
          <Typography className="py-1">
            {t("common:validate_reset_token")}
          </Typography>
        </Paper>
      ) : !loading && error ? (
        <Box className="position__centerlize center">
          <PageError
            text={error.message}
            onTryAgain={() => history.replace("/")}
            onTryText={t("common:back_home")}
          />
        </Box>
      ) : (
        <Slide
          in
          direction="left"
          timeout={400}
          // appear={mode === "login"}
          unmountOnExit
        >
          <RecreatePasswordSquare onNewPassword={onNewPassword} />
        </Slide>
      )}
    </div>
  );
};

export default ForgotPassword;
