import React from "react";
import { Box, Typography, Button, Paper } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import CachedIcon from "@material-ui/icons/Cached";
import ErrorOutlineRoundedIcon from "@material-ui/icons/ErrorOutlineRounded";

const PageError = ({ text, onTryAgain, onTryText, customItem }) => {
  const { t } = useTranslation();
  const Icon = customItem ? customItem : ErrorOutlineRoundedIcon;
  return (
    <Paper>
      <Box padding={5} className="center">
        <Icon color="error" style={{ fontSize: 200, opacity: 0.2 }} />
        <Typography variant="body2">{text}</Typography>
        {onTryAgain && (
          <Box className="m">
            <Button
              onClick={onTryAgain}
              color="primary"
              variant="outlined"
              endIcon={<CachedIcon />}
            >
              {onTryText || t("common:tryagain")}
            </Button>
          </Box>
        )}
      </Box>
    </Paper>
  );
};

export default PageError;
