import React, { useContext, useState } from "react";
import {
  Container,
  Box,
  Paper,
  Typography,
  Grid,
  Button,
  Fade,
} from "@material-ui/core";
import themeContext from "../../theme/themeContext";
import { useTranslation } from "react-i18next";
import Input from "../../components/Inputs/Input";
import i18n from "../../i18n";
import VisibilityIcon from "@material-ui/icons/Visibility";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";
import { useSelector } from "react-redux";
import ButtonSpinner from "../../components/UI/Spinners/ButtonSpinner";
import SwitchButtons from "../../components/Buttons/SwitchButtons";
import { isError } from "../../util/generals";

const initialState = {
  password: {
    value: "",
    validity: false,
    label: i18n.t("common:password"),
  },
};

const langBtns = [
  { name: "ar", content: "ðŸ‡¸ðŸ‡¦" },
  { name: "en", content: "ðŸ‡ºðŸ‡¸" },
];

const RecreatePasswordSquare = React.forwardRef(({ onNewPassword }, ref) => {
  const [state, setState] = useState(initialState);
  const [visible, setVisible] = useState(false);
  const { toggleLanguage } = useContext(themeContext);
  const { t } = useTranslation();
  const auth = useSelector((state) => state.auth);

  const handleInputResult = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: { ...state[name], value, validity },
      };
    });
  };

  const handleNewPassword = () => {
    const password = state.password.value;
    onNewPassword(password);
  };

  const canSubmit = state.password.validity;

  return (
    <Container className="fullheight" ref={ref}>
      <Fade in timeout={1500}>
        <Box className="fullheight flex__centerlize">
          <Grid container alignContent="center">
            <Grid item md={4} sm={3} xs={1} />
            <Grid item md={4} sm={6} xs={12}>
              <Paper className="p-1" elevation={3}>
                <Typography variant="h2" align="center">
                  {t("common:new_password")}
                </Typography>
                <Box className="p" />
                <Typography
                  variant="caption"
                  align="center"
                  component="div"
                  color="textSecondary"
                  className="py"
                >
                  {t("common:new_password_notice")}
                </Typography>
                <Input
                  label={t("common:password")}
                  name="password"
                  getResult={handleInputResult}
                  withIcon={
                    visible ? (
                      <VisibilityIcon
                        color="primary"
                        onClick={() => setVisible(!visible)}
                        className="hoverable-op"
                      />
                    ) : (
                      <VisibilityOffIcon
                        color="disabled"
                        onClick={() => setVisible(!visible)}
                        className="hoverable-op"
                      />
                    )
                  }
                  type={visible ? "text" : "password"}
                  onKeyDown={(k) =>
                    canSubmit && k.key === "Enter" ? handleNewPassword() : null
                  }
                  required
                  min={5}
                  max={21}
                  margin={4}
                  serverError={isError(
                    auth.errors && auth.errors.data,
                    `password`
                  )}
                />
                {auth.errors && (
                  <Typography
                    color="error"
                    variant="subtitle2"
                    className="py center"
                  >
                    {auth.errors.message}
                  </Typography>
                )}
                <Box className="center py-1">
                  <Button
                    color="primary"
                    variant="contained"
                    disabled={!canSubmit || auth.loading}
                    className="width60"
                    onClick={canSubmit ? handleNewPassword : null}
                    endIcon={auth.loading && <ButtonSpinner />}
                  >
                    {t("common:confirm")}
                  </Button>
                </Box>

                <Box className="center py">
                  <SwitchButtons
                    buttons={langBtns}
                    state={i18n.language}
                    onClick={(val) => {
                      if (val !== i18n.language) toggleLanguage(val);
                    }}
                  />
                </Box>
              </Paper>
            </Grid>
            <Grid item md={4} sm={3} xs={1} />
          </Grid>
        </Box>
      </Fade>
    </Container>
  );
});

export default RecreatePasswordSquare;
